<template>
  <v-row class="ma-sm-4">
    <v-col cols="12">
      <v-text-field
        v-model="searchText"
        solo
        flat
        hide-details="auto"
        background-color="#464646"
        label="Поиск пользователей"
        class="rounded-lg"
      >
        <template v-slot:prepend-inner>
          <v-icon class="mr-1">
            mdi-magnify
          </v-icon>
        </template>
      </v-text-field>
    </v-col>

    <v-col
      cols="12"
      sm="6"
      md="4"
    >
      <v-select
        v-model="deposited"
        :items="selectDeposited"
        item-value="key"
        item-text="text"
        solo
        flat
        hide-details="auto"
        clearable
        background-color="#464646"
        label="Наличие депозитов"
        class="rounded-lg"
        @change="firstPage"
      />
    </v-col>

    <v-col
      cols="12"
      sm="6"
      md="4"
    >
      <v-select
        v-model="role"
        :items="roles"
        solo
        flat
        hide-details="auto"
        clearable
        background-color="#464646"
        label="Роль"
        class="rounded-lg"
        @change="firstPage"
      />
    </v-col>

    <v-col
      sm="12"
      md="4"
    >
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="dates"
        transition="scale-transition"
        offset-y
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="dateRangeText"
            solo
            flat
            hide-details="auto"
            background-color="#464646"
            label="Даты регистрации"
            readonly
            class="rounded-lg"
            v-on="on"
          >
            <template v-slot:prepend-inner>
              <v-icon class="mr-1">
                mdi-calendar
              </v-icon>
            </template>
          </v-text-field>
        </template>
        <v-date-picker
          v-model="dates"
          no-title
          scrollable
          range
        >
          <v-spacer />

          <v-btn
            text
            color="primary"
            @click="menu = false"
          >
            Отмена
          </v-btn>
          <v-btn
            text
            color="primary"
            @click="saveDates"
          >
            ОК
          </v-btn>
        </v-date-picker>
      </v-menu>
    </v-col>

    <v-col cols="12">
      <v-card
        flat
        class="px-4 pb-2"
      >
        <v-card-title class="py-7">
          Пользователи: 2
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="items"
          :page.sync="page"
          :items-per-page="itemsPerPage"
          disable-sort
          no-data-text="Пользователи отсутствуют"
          no-results-text="Ничего не найдено"
          hide-default-footer
        >
          <template v-slot:[`item.img`]="{ item }">
            <v-avatar
              :color="item.img ? '' : '#c4c4c4'"
              size="36"
            >
              <img
                v-if="item.img"
                :src="item.img"
              >
              <v-icon
                v-else
                size="22"
                style="height: inherit"
              >
                mdi-account
              </v-icon>
            </v-avatar>
          </template>

          <template v-slot:[`item.createdAt`]="{ item }">
            {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
          </template>

          <template v-slot:[`item.action`]="{ item }">
            <v-btn
              :to="{ name: 'users', params: { id: item.id }}"
              depressed
              class="text-none"
              large
              height="36"
            >
              Подробнее
            </v-btn>
          </template>
        </v-data-table>
        <v-divider />

        <div class="text-center pt-3">
          <v-pagination
            v-model="page"
            :total-visible="9"
            :length="Math.ceil(2 / itemsPerPage)"
          />

          <v-select
            v-model="itemsPerPage"
            :items="[5, 10, 15]"
            label="Пользователей на странице"
            class="pa-4 pt-5"
            @change="firstPage"
          />
        </div>
      </v-card>
    </v-col>

    <v-col
      cols="12"
      class="text-center text-h5"
    >
      Уведомления
    </v-col>

    <v-col>
      <v-card
        flat
        class="pa-4"
      >
        <v-row>
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <v-text-field
              v-model="message"
              solo
              flat
              hide-details="auto"
              background-color="#464646"
              label="Сообщение"
              class="rounded-lg"
            />
          </v-col>
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <v-text-field
              v-model="from"
              solo
              flat
              hide-details="auto"
              background-color="#464646"
              label="От"
              class="rounded-lg"
            />
          </v-col>
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <v-autocomplete
              v-model="receivers"
              :items="items"
              solo
              flat
              hide-details="auto"
              clearable
              background-color="#464646"
              label="Получатели (по умолч. все)"
              item-text="username"
              item-value="id"
              class="rounded-lg"
              multiple
            >
              <template v-slot:selection="{ attrs, item, select, selected }">
                <v-chip
                  v-bind="attrs"
                  :input-value="selected"
                  close
                  @click="select"
                  @click:close="remove(item)"
                >
                  <v-avatar left>
                    <v-img :src="item.img" />
                  </v-avatar>
                  {{ item.username }}
                </v-chip>
              </template>

              <template v-slot:item="{ item }">
                <v-list-item-avatar>
                  <img :src="item.img">
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title>{{ item.username }}</v-list-item-title>
                  <v-list-item-subtitle>ID: {{ item.id }}</v-list-item-subtitle>
                </v-list-item-content>
              </template>
            </v-autocomplete>
          </v-col>
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <v-btn
              block
              depressed
              color="primary"
              class="text-none rounded-lg"
              height="48"
              x-large
            >
              Отправить
            </v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      searchText: '',
      searchTimeout: null,
      search: '',
      deposited: null,
      selectDeposited: [
        { text: 'Были депозиты', key: true },
        { text: 'Не было депозитов', key: false },
      ],
      role: '',
      roles: ['ROLE_UNDEFINED', 'USER', 'ADMINISTRATOR', 'SUPERUSER', 'PARTNER', 'BLOGGER', 'BANNED', 'PUBLIC'],
      menu: false,
      dates: [],
      registeredFrom: '',
      registeredTo: '',
      users: {},
      headers: [
        { text: '', value: 'img' },
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'Имя:', value: 'username', class: 'white--text text-subtitle-2' },
        { text: 'Баланс:', value: 'balance', class: 'white--text text-subtitle-2' },
        { text: 'Роль:', value: 'role', class: 'white--text text-subtitle-2' },
        { text: 'Дата регистрации:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
      page: 1,
      itemsPerPage: 5,
      message: '',
      from: '',
      receivers: [],
      items: [
        {
          img: 'https://demotivation.ru/wp-content/uploads/2020/01/Canada_Parks_Lake_Mountains_Forests_Scenery_Rocky_567540_3840x2400-scaled.jpg',
          id: 1,
          username: 'Test',
          balance: 300,
          role: 'USER',
          createdAt: 3333333333333,
        },
        {
          img: 'https://pbs.twimg.com/profile_images/913861131005022209/iaBdZZn1.jpg',
          id: 2,
          username: 'Andrew',
          balance: 0,
          role: 'USER',
          createdAt: 32131231233,
        },
      ],
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' ~ ');
    },
  },
  watch: {
    searchText(value) {
      clearTimeout(this.searchTimeout);
      this.searchTimeout = setTimeout(() => {
        this.firstPage();
        this.search = value;
      }, 500);
    },
  },
  methods: {
    firstPage() {
      this.page = 1;
    },
    saveDates() {
      this.$refs.menu.save(this.dates);
      [this.registeredFrom, this.registeredTo] = this.dates;
    },
    remove(item) {
      const index = this.receivers.indexOf(item.id);
      if (index >= 0) this.receivers.splice(index, 1);
    },
    onNotification() {
      this.message = '';
      this.from = '';
      this.receivers = [];
    },
  },
};
</script>
